<template>
    <div class="pafChart_box" style='width:734px;height:282px'>
        <div class="title_box" ><span>{{title}}</span></div>
        <div class="content_box">
            <!-- 饼状 -->
            <div class="list_content" v-if="type=='pie'">
                <div class="list" >
                    <span class="dw">单位：万元</span>
                </div>
                <div class="list" >
                    <div class="text_color" style="background-color:#FFDF99"></div>
                    <span>基金规模</span>
                </div>
                <div class="list" >
                    <div class="text_color" style="background-color:#FFAD58"></div>
                    <span>已投金额</span>
                </div>
            </div>
             <!-- 柱状 -->
            <div class="list_content" v-if="type=='bar'">
                <div class="list" >
                    <span class="dw">单位：个</span>
                </div>
                <div class="list" >
                    <div class="text_color" style="background-color:#FF5858"></div>
                    <span>舆情数量</span>
                </div>
            </div>
            <!--  横着柱状-->
            <div class="list_content" v-if="type=='bar1'">
                <div class="list" >
                    <span class="dw">单位：个</span>
                </div>
                <div class="list" >
                    <div class="text_color" style="background-color:#7ED321"></div>
                    <span>已投项目数量</span>
                </div>
                <div class="list" >
                    <div class="text_color" style="background-color:#B8E986"></div>
                    <span>退出项目数量（全部退出）</span>
                </div>
                <div class="list" >
                    <div class="text_color text_color1" ></div>
                    <span>退出项目数量（部分退出）</span>
                </div>
            </div>
            <div class="picture_content">
                <!-- 饼状 -->
                <div id="myChartPie" :style="{width:'400px',height:'250px'}" v-if="type=='pie'">
                </div>
                <!-- 柱状 -->
                <div :id="idName" :style="{width:'400px',height:'250px'}" v-if="type=='bar'">
                </div>
                <!--  横着柱状-->
                <div v-if="type=='bar1'" class="bar1">
                    <div class="line_top_box">
                        <div class="line_style line_style1" :style="{'width':(qbtc/ytxm)*100+'%'}" v-if="(qbtc/ytxm)*100>0">
                             <div class="line_content_style line_content_style1"><span>{{qbtc}}</span></div>
                        </div>
                        <div class="line_style line_style2" :style="{'width':(bftc/ytxm)*100+'%'}" v-if="(bftc/ytxm)*100>0">
                            <div class="line_content_style line_content_style1"><span>{{bftc}}</span></div>
                        </div>
                    </div>
                    <div class="block_middle_box">
                        <div class="block_style1" :style="{'width':(qbtc/ytxm)*100+'%'}"></div>
                        <div class="block_style2" :style="{'width':(bftc/ytxm)*100+'%'}"></div>
                    </div>
                    <div class="line_bottom_box">
                        <div class="line_style line_style3">
                            <div class="line_content_style "><span>{{ytxm}}</span></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        dialogConfirm,
        dialogConfirmHasbtn,
        compareJson
    } from "../../config/mUtils";
    import echarts from "echarts";
    
    export default {
        data() {
            return {
                percent: 0,
                totla: 100,
                num: 10,
                monthArr:[],
                dataObj :{},
                tmp1:[],
                tmp2:[],
            };
        },
        props: ["type","title","idName",'value','total','ytxm','bftc','qbtc','newData'], //
        components: {},
        created() {
            if(this.newData){
                this.newData.forEach(item => {
                    let month = '';
                    if(item.time){
                        month = item.time.split('-')[1];
                    }else if(item.start_time){
                        month = item.start_time.split('-')[1];
                    }
                    if(parseInt(month)<10){
                        month = month.replace("0","");
                    }
                    if(this.monthArr.indexOf(month)==-1){
                        this.monthArr.push(month);
                        this.dataObj[month] = 1;
                    }else{
                        this.dataObj[month] =this.dataObj[month] + 1;
                    }
                });
                let tmp1 = [];
                let tmp2 = [];
                let dataObj = this.dataObj;
                Object.keys(this.dataObj).sort().forEach(function (k) {
                    tmp1.push(k+'月');
                    tmp2.push(dataObj[k]);
                });
                this.tmp1 = tmp1;
                this.tmp2 = tmp2;
                this.tmp2.push(10);
            }
        },
        computed: {},
        mounted() {
            if (this.type == "pie") {
                let myChartPie = echarts.init(document.getElementById("myChartPie"));
                // let total = parseInt(this.total);//总数
                // let value1 =  parseInt(this.value);//值1
                // let value = total - value1;//剩余值
                myChartPie.setOption({
                    series: {
                        type: "pie", //类型
                        radius: "74px", //半径
                        center: ["50%", "40%"], //位置
                        data: [{
                                // value: parseInt(this.total)  - parseInt(this.value),
                                value:parseFloat(this.total) - parseFloat(this.value) ,
                                name: parseFloat(this.total),
                                itemStyle: {
                                    color: "#FFD985"
                                }
                            },
                            {
                                // value: parseInt(this.value),
                                value:parseFloat(this.value),
                                name: parseFloat(this.value),
                                itemStyle: {
                                    color: "#FFAD58"
                                }
                            }
                        ],
                        hoverAnimation: false, //取消hover放大
                        legendHoverLink: false //取消hover高亮
                    },
                    textStyle: {
                        color: "#333333"
                    }
                });
            } else if (this.type == "bar") {
                let myChartBar = echarts.init(document.getElementById(this.idName));
                myChartBar.setOption({
                    color: ["#FF5858"],
                    grid: {
                        left: "3%",
                        right: "4%",
                        bottom: "10%",
                        // top:'10%',
                        containLabel: true
                    },
                    xAxis: [{
                        type: "category",
                        data: this.tmp1,
                        splitLine: {
                            show: false
                        }, //去除网格线
                        axisTick: {
                            alignWithLabel: true
                        },
                        axisLine: {
                            lineStyle: {
                                color: "#E7E8EA"
                            }
                        },
                        axisLabel: {
                            textStyle: {
                                color: "#282828"
                            }
                        }
                    }],
                    yAxis: [{
                        type: "value",
                        splitLine: {
                            show: false
                        }, //去除网格线
                        axisLine: {
                            lineStyle: {
                                color: "#E7E8EA"
                            }
                        },
                        axisLabel: {
                            textStyle: {
                                color: "#282828"
                            }
                        },
                        label:{
                            show:false,
                        }
                    }],
                    series: [{
                        name: "直接访问",
                        type: "bar",
                        barWidth: "35%",
                        data: this.tmp2,
                        label:{
                            normal: {
                                show: true,
                                position: 'top',
                                color:'#A0A4AA'
                            },
                            
                        }//是否显示柱状图上
                    }]
                });
            }
        },
        methods: {
            // countPrecent() {
            //     this.percent = this.num / this.totla;
            //     this.percent = this.percent.toFixed(3) * 360;
            //     if (this.percent >= 360) {
            //         this.percent = 360;
            //     }
            //     let style = document.createElement("style");
            //     style.innerHTML =
            //         ".picture_content_circle::before{ transform:rotate(" +
            //         this.percent +
            //         "deg)}"; 
            //     document.head.appendChild(style);
            // }
        }
    };
</script>

<style lang="scss" scoped>
    @import "../../style/folder.scss";
    @import "../../style/fund1.scss";
    .pafChart_box .title_box {
        background-color: #fff6ee;
        font-size: 17px;
        padding: 4px 13px;
        
    }
    
    .pafChart_box .content_box {
        color: #a0a4aa;
        font-size: 16px;
        display: flex;
    }
    
    .pafChart_box .list_content {
        padding: 30px 32px;
    }
    
    .pafChart_box .list {
        display: flex;
        align-items: center;
        margin-bottom: 13px;
    }
    
    .pafChart_box .text_color {
        width: 10px;
        height: 10px;
        margin-right: 10px;
    }
    .pafChart_box .text_color1{
        background: url('../../images/icon_xx1.png') no-repeat;
        background-size: 100% 100%;
    }
    .pafChart_box .list_content span {
        font-size: 12px;
        color: #808080;
    }
    
    .pafChart_box .list_content .dw {
        color: #a0a4aa;
    }
    
    .pafChart_box .picture_content {
        position: relative;
        margin-left: 100px;
    }
    
    
    .bar1{
        margin-top: 60px;
    }

    .block_middle_box{
        width: 335px;
        height: 36px;
        background-color: #7ED321;
        display: flex;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .block_style1{
        // width: 100px;
        height: 36px;
        background-color: #B8E986;
    }
    .block_style2{
        // width: 21px;
        height: 36px;
        background-color: #B8E986;
        background: url('../../images/icon_xx3.png');
        background-size: cover;
    }
    .line_top_box,.line_top_box{
        display: flex;
    }
    .line_style{
        height: 11px;
        display:flex;
        align-items: center;
        border-left: 2px solid #F1F1F1;
        border-right: 2px solid #F1F1F1;
        
    }
    .line_style1{
        // width: 100px;
        border-right:none
    }
    .line_style2{
        // width: 21px;
    }
    .line_style3{
        width: 100%;
    }
    .line_content_style{
        width: 100%;
        height: 2px;
        background-color: #F1F1F1;
        text-align: center;
    }
    .line_content_style span{
        font-size: 12px;
        color: #808080;
        position: relative;
        top: 0px;
    }
    .line_content_style1 span{
         top: -20px;
    }
</style>



