                projectDetailData: [],
                companyData: [],
                reportData: "",
                evaluate:'',
                pafSrc :'',
                fund_id:'',
                companyQuite:{},
                company_quite:[],
                // newData:[{
                //     title: '111',
                //     source: '11',
                //     time: '2018-05-03',
                // },{
                //     title: '22222',
                //     source: '222',
                //     time: '2018-03-04',
                // },{
                //     title: '3333',
                //     source: '333',
                //     time: '2018-06-14',
                // },{
                //     title: '444',
                //     source: '444',
                //     time: '2018-03-04',
                // },{
                //     title: '555',
                //     source: '555',
                //     time: '2018-05-08',
                // },],
                isCanvas:true,
                imgSrcArr:[],
                loadingInstance:'',
                type:'',
            };
        },
        components: {partnerList},
        watch: {
        },
        created() {
            // console.log(getStore('pdfData'));
            let pdfData = JSON.parse(getStore("pdfData"));
            this.fundBaseInfo = pdfData.fund_base_info;
            this.companyCount = pdfData.company_count;
            this.partnerTable = pdfData.partnerTable;
            this.projectTableData = pdfData.projectTableData;
            this.projectDetailData = pdfData.projectDetailData;
            this.companyData = pdfData.companyData;
            this.evaluate = pdfData.evaluate;
            this.durationKey =parseInt(this.fundBaseInfo.duration1) + parseInt(this.fundBaseInfo.duration2)+parseInt(this.fundBaseInfo.duration3);
            this.reportData = new Date().Format(
                "yyyy" + "年" + "MM" + "月" + "dd" + "日"
            );
            this.fund_id = this.$route.query.fundId;
            this.companyQuite = pdfData.companyQuite;
            this.company_quite = pdfData.company_quite;
            this.type = this.$route.query.type;
        },
        computed: {},
        mounted() {},
        methods: {
            toDown(){
                if(!this.pafSrc){
                    this.imgSrcArr = this.imgSrcArr.reverse();
                    this.imgSrcArr.forEach((element,index) => {
                        let cacansImg = document.getElementsByClassName('htmltoImg')[index];
                        let canvasElemnt = document.getElementsByClassName('pafChart_box')[index];
                        canvasElemnt.style.display='none';
                        canvasElemnt.innerHTML = '';
                        // canvasElemnt.parentNode.removeChild(canvasElemnt)
                        cacansImg.src = element;
                    });
                    
                    let titleHtml = '';
                    let pdfTitle = document.getElementById('pdfTitle');
                    let pdfContent = document.getElementById('pdfContent');
                    // console.log(pdfContent.innerHTML)
                    reportSave({
                        title:this.fundBaseInfo.fundName+'投资人分析报告',
                        fund_id:this.fund_id,
                        user_id:getStore('htcx_uid'),
                        type:this.type,
                        head_content:pdfTitle.innerHTML,
                        content:pdfContent.innerHTML,
                        
                    }).then(res=>{
                        if (res.data.msgNo == 0) {
                            // this.loadingInstance.close();
                            // console.log('加载完成');
                            this.loadingInstance.close();
                            this.pafSrc = res.data.payload.preview_file_path;
                            let tempwindow = window.open("_blank"); // 先打开页面
                            tempwindow.location = this.pafSrc; // 后更改页面地址
                        }else{
                            dialogConfirm('info',res.data.message);
                        }
                    })
                }else{
                    let tempwindow = window.open("_blank"); // 先打开页面
                    tempwindow.location = this.pafSrc; // 后更改页面地址
                }
                
            },
            togetImg(){
                if(this.isCanvas){
                    let pafChartboxArrLength = document.getElementsByClassName('pafChart_box').length;
                    this.loadingInstance = Vue.prototype.$loading({
                        background: 'rgba(0,0,0,0.5)',
                        text: '生成中'
                    });
                    this.htmlToImg(pafChartboxArrLength);
                }else{
                     this.toDown();
                }
            },
            htmlToImg(pafChartboxArrLength){
                if(pafChartboxArrLength!=0){
                    let _this = this;
                    // let scaleNumber = 2;
                    // let w =  document.getElementsByClassName('pafChart_box')[pafChartboxArrLength-1].style.width;    // 获得该容器的宽
                    // let h =  document.getElementsByClassName('pafChart_box')[pafChartboxArrLength-1].style.height; 
                    // // w = window.getComputedStyle(w)   
                    // //  h = window.getComputedStyle(h)
                    // console.log(w);
                    // console.log(h);
                    // return false;
                    // document.getElementsByClassName('pafChart_box')[pafChartboxArrLength-1].style.WebkitTransform="scale("+scaleNumber+","+scaleNumber+")";
                    
                    html2canvas(document.getElementsByClassName('pafChart_box')[pafChartboxArrLength-1], {
                        backgroundColor: '#fff',
                        // allowTaint:true,
                        scale:2,
                    }).then(function(canvas) {
                        // console.log(canvas);
                        // canvas.width = 1468;
                        // canvas.height = 564;

                        // canvas.style.width = '1468px';
                        // canvas.style.height = '564px';
                        // canvas.height='564';

                            // canvas.width = 1468;    // 将画布宽&&高放大两倍
                            // canvas.height = 564;
                            // let context = canvas.getContext("2d");
                            // context.scale(2, 2);
                            // context.translate(-offsetLeft-abs,-offsetTop); 



                        _this.imgSrcArr.push(canvas.toDataURL("image/png"));
                        _this.htmlToImg(pafChartboxArrLength-1);
                    });
                }else{
                    this.isCanvas = false;
                    this.toDown();
                }
            }
            
        }
    };
</script>

